.rs-ssb-main {
  width: 100%;
}

.rs-ssb-main__item {
  position: relative;

  &::before {
    display: block;
    padding-top: 59.535655058%;
    content: '';
  }

  .video-yt {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: all;

    &::after {
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      display: block;
      width: 64px;
      height: 64px;
      margin-top: -32px;
      margin-left: -32px;
      content: '';
      transition-duration: 0.2s;
      pointer-events: none;
      background: url(../img/play.svg) no-repeat center center;
      background-size: contain;
    }

    &:hover {
      &::after {
        transform: scale(1.1);
      }
    }

    &.video-yt-has-iframe {
      &::after {
        display: none;
      }
    }
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-position: center center;
    object-fit: cover;
  }
}

.rs-ssb-thumbs {
  width: 100%;
  margin-top: 2px;

  .slick-list {
    margin: 0 0 0 -1px;
  }
}

.rs-ssb-thumbs-item {
  position: relative;
  padding: 0 1px;
  cursor: pointer;

  &--video {
    &::after {
      position: absolute;
      z-index: 1;
      top: 50%;
      left: 50%;
      display: block;
      width: 39px;
      height: 39px;
      margin-top: -19.5px;
      margin-left: -19.5px;
      content: '';
      background: url(../img/play.svg) no-repeat center center;
      background-size: contain;
    }
  }
}

.rs-ssb-thumbs-item-inner {
  position: relative;
  width: 100%;

  &::before {
    display: block;
    padding-top: 92.25%;
    content: '';
  }

  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-position: center center;
    object-fit: cover;
  }
}

.slider-white-prev,
.slider-white-next {
  position: absolute;
  z-index: 1;
  top: 50%;
  width: 29px;
  margin-top: -14.5px;
  cursor: pointer;
  transition-duration: 0.2s;

  svg {
    width: 100%;
    height: auto;
  }

  &:hover {
    opacity: 0.7;
  }
}

.slider-white-prev {
  left: 16px;
}

.slider-white-next {
  right: 16px;
}

