.btn {
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  box-sizing: border-box;
  padding: 13px 31px 12px 40px;
  transition-duration: 0.2s;
  letter-spacing: 0.1em;
  font-weight: normal;
  font-size: 16px;
  line-height: 132%;
  font-style: normal;
  position: relative;

  @media (max-width: 650px) {
    padding: 13px 10px 12px 10px;

  }

  &--white {
    color: #ffffff;
    border: 1px solid #ffffff;

    svg {

      circle {
        fill: #FFE81B;
      }
    }

    &:hover {
      border: 1px solid #FFE81B;
      background-color: #FFE81B;
      color: #232323;
      border: 1px solid #232323;

      svg {
        path:last-child {
          stroke: #232323;
        }

        circle {
          fill: #ffffff;
        }
      }
    }
  }

  &--grey {
    color: #9d9ebf;
    border: 1px solid #9d9ebf;

    svg {
      path {
        stroke: #2c2e85;
      }

      circle {
        fill: #9d9ebf;
      }
    }

    &:hover {
      color: #ffffff;
      border: 1px solid #ffffff;

      svg {
        path {
          stroke: #ffffff;
        }

        circle {
          fill: #FFB800;
        }
      }
    }
  }

  &--orange {
    color: #232323;
    border: 1px solid #FFE81B;
    background-color: #FFE81B;

    svg {
      path {
        stroke: #FFE81B;
      }

      circle {
        fill: #ffffff;
      }
    }

    &:hover {
      opacity: 0.7;
    }
  }

  &--with-right-arrow {
    padding-right: 50px;



    @media (max-width: 650px) {
      padding-right: 25px;
    }

    svg {
      display: block;
      min-width: 20px;
      height: 20px;
      margin-left: 14px;
      content: "";

      &,
      & * {
        transition-duration: 0.2s;
      }
    }
  }

  &--triangle-arrow {
    svg {
      display: block;
      min-width: 20px;
      height: 100%;
      margin-left: 14px;
      content: "";
      position: absolute;
      right: 0;

      @media (max-width: 1026px) {
        right: -2px;
      }
    }

    @media (max-width: 1026px) {
      @media (max-width: 1026px) {
        border: 1px solid transparent;

        svg {
          path:first-child {
            fill: #232323;
          }

          path:first-child {
            stroke: #232323;
          }
        }
      }
    }
  }
}

.customized-ul-wrap {
  ul {
    list-style: none;
  }

  li {
    display: flex;
    align-items: flex-start;
