    line-height: 150%;
    font-style: normal;

    &:not(:last-child) {
      margin-bottom: 17px;
    }

    &::before {
      display: block;
      min-width: 7px;
      min-height: 7px;
      margin-top: 7px;
      margin-right: 16px;
      content: "";
      border-radius: 50%;
      background: #555665;
    }
  }
}

