.oc-slider-controls {
  position: relative;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  box-sizing: border-box;
  width: 81px;
  margin: 48px 0;
  border-left: 1px solid #dadae8;

  .slick-dots {
    display: flex;
    align-items: center;
    flex-direction: column;
    flex-grow: 1;
    justify-content: center;
    order: 1;
    list-style: none;

    li {
      position: relative;
      display: block;
      width: 7px;
      height: 7px;
      margin: 12px 0;
      cursor: pointer;
      border-radius: 50%;
      background: #c7c7d5;

      button {
        display: none;
      }

      &::before {
        position: absolute;
        z-index: 1;
        top: -1px;
        left: -1px;
        display: block;
        width: 9px;
        height: 9px;
        content: "";
        transition-duration: 0.2s;
        opacity: 0;
        border-radius: 50%;
        background: #FFE81B;
      }

      &:hover {
        &::before {
          opacity: 0.5;
        }
      }

      &.slick-active {
        &::before {
          opacity: 1;
        }
      }
    }
  }
}

.oc-slick-prev.slick-arrow,
.oc-slick-next.slick-arrow {
  width: 18px;
  margin: 0;
  cursor: pointer;

  svg {
    display: block;
    height: 9px;
    margin: 0;

    path {
      transition-duration: 0.1s;
    }
  }

  &:hover {
    svg {
      path {
        stroke: #ffb800;
      }
    }
  }
}

.oc-slick-prev {
  order: 0;
}

.oc-slick-next {
  order: 2;
}

