.video-slider-affd-wrap {
  margin-top: -2px;
  padding: 0 16px 0 28px;

  .rs-ssb-main__item::before {
    padding-top: 65.9%;
  }
}

.rs-ssb-main--affd-vs {

  .slick-dots {
    position: absolute;
    bottom: 30px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;


    li {
      cursor: pointer;
      display: block;
      margin: 0 9px;
      width: 6px;
      height: 6px;
      background: #FFFFFF;
      border-radius: 50%;
      position: relative;

      #slick-slide-control22 {
        &:hover {}

        &:active {}

        &:focus {}
      }

      button {
        display: none;
      }

      &::before {
        content: '';
        display: block;
        background: #FFFFFF;
        width: 10px;
        height: 10px;
        border-radius: 50%;
        left: -2px;
        top: -2px;
        position: absolute;
        opacity: 0;
        transition-duration: .2s;
      }

      &.slick-active,
      &:hover {
        &::before {
          opacity: 1;
        }
      }
    }
  }
}

.affd-vs-item-name {
  box-sizing: border-box;
  position: absolute;
  width: 100%;
  padding: 5% 15px 0;
  text-align: center;

  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 150%;
  /* or 18px */

  text-align: center;

  color: #FFFFFF;
  z-index: 1;

}